.about {
  background: url(../img/bg/gray.jpg) no-repeat center / cover;
  padding: 30px 0 40px;
  &__inner {
    display: flex;
    align-items: flex-start;
    // justify-content: space-between;
  }
  &__content {
    padding-top: 60px;
    max-width: 560px;
  }
  &__header {
    margin-bottom: 80px;
  }
  &__person {
    margin-right: 60px;
  }
  &__list {}
}

.person {
  $brs-inflated: 50% 15px;
  // position: relative;
  &__photo {
    position: relative;
    z-index: 1;
  }
  &__img {
    margin: 0 auto;
  }
  &__card {
    width: 390px;
    // width: 100%;
    margin: -120px auto auto;
    background-color: #f1f1f1;
    border: 8px solid #ffffff;
    border-top-left-radius: $brs-inflated;
    border-top-right-radius: $brs-inflated;
    border-bottom-left-radius: $brs-inflated;
    border-bottom-right-radius: $brs-inflated;
    text-align: center;
    padding: 45px 15px 35px;
    position: relative;
    z-index: 2;
    // position: absolute;
  }
  &__title {
    font-style: oblique;
    font-size: $fz-subtitle;
    line-height: 30px;
    margin-bottom: 20px;
  }
  &__subtitle {
    font-style: oblique;
    font-size: 1.125rem;
    line-height: 30px;
    & > span {
      text-decoration: underline;
    }
  }
}

.features {
  &__title {
    font-size: $fz-subtitle;
    line-height: 30px;
    font-weight: 800;
    margin-bottom: 25px;
  }
  &__list {
    padding-left: 30px;
  }
  &__item {
    font-size: $fz-subtitle;
    line-height: 30px;
    margin-bottom: 40px;
    position: relative;
    &::before {
      content: '';
      display: block;
      width: 12px;
      height: 12px;
      background-color: #fff;
      border-radius: 50%;
      position: absolute;
      left: -30px;
      top: 10px;
    }
  }
}