.contacts {
  background: url(../img/bg/map.jpg) no-repeat right center / cover;
  padding: 80px 0 90px;
  &__inner {
    display: flex;
  }
  &__column {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__title {
    font-size: $fz-title;
    font-weight: 800;
    text-transform: uppercase;
    margin-bottom: 80px;
  }
  &__row {
    font-size: $fz-subtitle;
    margin-bottom: 45px;
  }
  &__tel {
    font-size: $fz-title;
    font-weight: 800;
    text-transform: uppercase;
    &:hover {
      text-decoration: underline;
    }
  }
  &__btn {}
}