.price {
  $brs-inflated: 80% 50px;
  padding: 35px 0 40px;
  &__inner {

  }
  &__card {
    background-color: #fff;
    padding: 50px 100px 35px;
    border-top-left-radius: $brs-inflated;
    border-top-right-radius: $brs-inflated;
    border-bottom-left-radius: $brs-inflated;
    border-bottom-right-radius: $brs-inflated;
    box-shadow: 0px 17px 35px 0px rgba(193,187,180,0.5);
  }
  &__header {}
  &__list {}
}

.price-list {
  // --rad: 45% 10px;
  $brs-inflated: 45% 10px;
  margin: 0 auto;
  max-width: 880px;
  display: flex;
  flex-direction: column;
  &__item {
    min-height: 110px;
    padding: 25px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid transparent;
    transition: .15s;

    border-top-left-radius: $brs-inflated;
    border-top-right-radius: $brs-inflated;
    border-bottom-left-radius: $brs-inflated;
    border-bottom-right-radius: $brs-inflated;
    &:hover {
      cursor: pointer;
      border: 1px solid $accent-secondary;
      box-shadow: 0px 7px 20px 0px rgba(255,207,163,0.2);
    }
  }
  &__text, &__value {
    font-size: $fz-subtitle;
    line-height: 30px;
  }
  &__text {
    padding-right: 30px;
  }
  &__value {
    max-width: 180px;
    // padding-right: 30px;
    // padding-left: 60px;
    width: 100%;
    white-space: nowrap;
    text-align: center;
    position: relative;
    &::before {
      content: '';
      display: block;
      position: absolute;
      top: calc(50% - 37px);
      left: 0;
      width: 1px;
      height: 75px;
      background-color: $accent-secondary;
    }
  }
}