.footer {
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 40px 95px;
    position: relative;
  }
  &__copy {
    font-size: $fz-default;
    font-weight: 300;
  }
  &__logo {
    // margin: 0 auto;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  &__links {}
  &__link {
    font-size: $fz-default;
    text-decoration: underline;
    & + & {
      margin-left: 60px;
    }
  }
}