.guarantee {
  $brs-inflated: 80% 20px;
  background: url(../img/bg/guarantee.png) no-repeat right bottom / auto 85%;
  padding: 95px 0 25px;
  &__inner {}
  &__row {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  &__header {
    margin-bottom: 5px;
  }
  &__item {
    &--glove .guarantee__card {
      margin-top: 185px;
      margin-left: 40px;
    }
    &--mob {
      display: none;
    }
  }
  &__text-mob {
    margin: 0 auto;
    font-size: $fz-subtitle-mob;
    line-height: 16px;
  }
  &__glove {
    width: 410px;
    height: 607px;
    background: no-repeat center / cover;
    background-image: url(../img/content/glove.png);

    position: relative;
    & > .guarantee__card {
      position: relative;
      top: 75px;
      left: 100px;
    }
    &::after {
      // content: url(../img/content/finger.png);
      content: '';
      background: no-repeat left bottom / cover;
      background-image: url(../img/content/finger.png);
      display: block;
      position: absolute;
      bottom: 3px;
      left: 0;
      width: 204px;
      height: 409px;
    }
  }
  &__card {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 35px 25px;
    width: 290px;
    border: 2px solid #fc9e49;
    border-top-left-radius: $brs-inflated;
    border-top-right-radius: $brs-inflated;
    border-bottom-left-radius: $brs-inflated;
    border-bottom-right-radius: $brs-inflated;
  }
  // &__text {
  //   display: none;
  // }
  &__card-title {
    font-size: 3.125rem;
    font-weight: 800;
    text-transform: uppercase;
    margin-bottom: 15px;
  }
  &__card-text {
    line-height: 30px;
  }
  &__img {
    position: relative;
    right: 20px;
    top: 50px;
  }
}

@media (min-width: 1200px) {}
@media (min-width: 992px) and (max-width: 1199px) {}
@media (min-width: 768px) and (max-width: 991px) {}
@media (max-width: 767px) { //TODO this section
  .guarantee {
    background-size: 80% auto;
    & .section-header__subtitle {
      display: none;
    }
    &__row {
      flex-direction: column;
    }
    &__item {
      &--mob {
        display: block;
      }
      &--glove .guarantee__card {
        margin-top: 0;
        margin-left: 0;
      }
    }
    &__glove {
      width: 160px;
      height: 236px;
      // background-size: cover;
      & > .guarantee__card {
        // position: static;
        top: 15px;
        left: 60px;
      }
      &::after {
        // content: '';
        // display: block;
      }
    }
    &__card {
      padding: 20px 5px 20px 15px;
      width: 160px;
    }
    &__card-title {
      font-size: $fz-subtitle;
      margin-bottom: 0;
    }
    &__card-text {
      font-size: $fz-default-mob;
      line-height: 16px;
    }
  }
}
